.js-more--open .js-more__item:nth-child(n) {
  display: block; }

.js-more--open .js-more__icon {
  transform: rotate(180deg); }

.js-more--open .js-more__text--show {
  display: none; }

.js-more--open .js-more__text--hide {
  display: block; }

.js-more__btn {
  overflow: hidden; }
  .js-more__btn:hover {
    opacity: 0.7; }

.js-more__icon {
  transition: inherit; }

.js-more__text--hide {
  display: none; }
